.App {
  text-align: center;
  height: 100%;
  position: relative;
}

.console {
  position: absolute;
  z-index: 10;
  text-align: center;
  width: 100%;
}

.fade-enter{
  opacity: 0;
}
.fade-exit{
  opacity: 1;
}
.fade-enter-active{
  opacity: 1;
}
.fade-exit-active{
  opacity: 0;
}
.fade-enter-active,
.fade-exit-active{
  transition: opacity 2000ms;
}